import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { DomSanitizer } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { FormListComponent } from '@fragments';
import { Company } from '@models';
import { ApiService, DataStorageService, SweetAlertService } from '@services';
import { ATTACHMENT_TYPE, COMMON_FUNCTION, ENDPOINTS, STATUSES, SUBMISSION_TYPE } from '@shared';
import { PaymentMethod } from 'app/core/models/payment-method.model';
import { VoucherSignatories } from 'app/core/models/voucher-signatories.model';
import { Voucher } from 'app/core/models/voucher.model';
import { FileStorageService } from 'app/core/services/file.storage.service';
import Swal from 'sweetalert2';


@Component({
  selector: 'app-company-voucher-enrollment',
  templateUrl: './company-voucher-enrollment.component.html',
  styleUrls: ['./company-voucher-enrollment.component.scss']
})
export class CompanyVoucherEnrollmentComponent implements OnInit, OnDestroy {

  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<Voucher>;
  displayedColumns: string[] = ['company', 'fundAmount', 'voucherDate'];
  dataSourceSignatories: MatTableDataSource<VoucherSignatories>;
  displayedColumnsSignatories: string[] = ['name', 'position', 'voucherActionItems'];
  @Input() safeURL: any;

  voucher = new Voucher();
  company = new Company();
  voucherSignatories = new VoucherSignatories();
  paymentMethod = new PaymentMethod();

  voucherList = [];
  companyList = [];
  attachmentDocuments: any;
  statusList = [...STATUSES];
  signatoriesList = [];

  selectedFiles: any;
  attachment: any;
  // safeURLVoucherEnrollment: any;
  process = 'save';

  previewDocument = false;
  currentDate = new Date();

  voucherForm: FormGroup;
  voucherSignatoriesForm: FormGroup;
  resetForm: any;

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sanitizer: DomSanitizer,
    public dataStorage: DataStorageService,
    private fileStorageService: FileStorageService,
    private sweetAlertService: SweetAlertService,
    private router: Router,

  ) {

  }

  ngOnInit(): void {
    this.setFormValidator();
    this.setSignatoriesForm();
    this.getVoucherList();
    this.getCompanyList();
  }

  ngOnDestroy(): void {

  }

  setFormValidator(): void {
    this.voucher.status = COMMON_FUNCTION.ACTIVE;
    this.voucherForm = this.formBuilder.group({
      company: ['', [Validators.required]],
      fundAmount: ['', [Validators.required]],
      voucherDate: ['', [Validators.required]],
      // status: ['', [Validators.required]],
    })


  }

  setSignatoriesForm() {
    this.voucherSignatoriesForm = this.formBuilder.group({
      voucherSignatoriesName: ['', [Validators.required]],
      voucherSignatoriesPosition: ['', [Validators.required]],
    })
  }

  resetsetSignatoriesForm() {
    this.voucher.status = COMMON_FUNCTION.ACTIVE;
    this.voucherSignatories = new VoucherSignatories();
    this.voucherSignatoriesForm.markAsUntouched();
    this.voucherSignatoriesForm.markAsPristine();
  }

  resetFormValidatorVoucher(): void {
    this.voucher.status = COMMON_FUNCTION.ACTIVE;
    this.voucher = new Voucher();
    this.attachment = null;
    this.voucherForm.markAsUntouched();
    this.voucherForm.markAsPristine();
  }

  selectFile(event) {
    const attachmentSize = event.target.files[0].size;

    if (event.target.files[0].type === 'application/pdf') {
      if (attachmentSize > 15000000) { // 1MB = 1M
        this.sweetAlertService.customErrorMessage(`Attachments' total file size should not exceed 15MB.`);
        event.target.value = '';
        return;
      } else {
        const pdfURL = URL.createObjectURL(event.target.files[0]);
        this.safeURL = this.sanitizer.bypassSecurityTrustResourceUrl(pdfURL);
        this.previewDocument = true;
        const reader = new FileReader();
        const [file] = event.target.files;
        reader.readAsDataURL(file);
        reader.onload = () => {
        };
      }
      this.selectedFiles = event.target.files;
    } else {
      this.sweetAlertService.customErrorMessage('Attachment should be in PDF format only.');
      event.target.value = '';
      return;
    }
  }

  uploadVoucher() {
    Swal.fire({
      text: 'Are you sure you want to submit this VOUCHER?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.dataStorage.setDisplayProgressbar(true);
        let params = '';
        params = '?&attachmentType=' + ATTACHMENT_TYPE.VOUCHER_FUND + '&submissionType=' + SUBMISSION_TYPE.ORIGINAL;
        this.fileStorageService.pushFileToStorage(ENDPOINTS.attachmentDocument, this.selectedFiles.item(0), params)
          .subscribe(res => {
            const obj = res.responseData.data;
            // console.log('UPLOADED', obj);
            this.voucher.voucherAttachmentReference = obj.voucherAttachmentReference;
            this.voucher.company = this.company;
            this.voucher.voucherSignatories = this.signatoriesList;
            // console.log('VOUCHER', this.voucher);
            this.saveVoucher();
            this.selectedFiles = undefined;
          }, (err: any) => {
            this.sweetAlertService.error(err);
          }, () => {
            this.dataStorage.setDisplayProgressbar(false);
          });
      }
    });
  }

  viewAttachmentFile() {
    this.apiService.findByParam(ENDPOINTS.attachmentDocument, `voucher-attachment-reference/` + this.voucher.voucherAttachmentReference)
      .subscribe((res1: any) => {
        if (res1) {
          this.previewDocument = true;
          this.attachmentDocuments = res1.responseData.data;
          this.safeURL = this.sanitizer.bypassSecurityTrustResourceUrl(this.attachmentDocuments.fileURL);
          // console.log('view attachmentDocument: ', this.attachmentDocuments);
        }
      }, (err: any) => {
        this.attachmentDocuments = [];
      });
  }

  reset() {
    this.process = 'save';
    this.resetFormValidatorVoucher();
    this.resetsetSignatoriesForm();
    this.attachmentDocuments = [];
    this.previewDocument = false;
    this.dataSourceSignatories = null;
    this.signatoriesList = [];
  }


  saveVoucher() {
    // console.log('SAVE ::: ', this.voucher);
    this.apiService.save(ENDPOINTS.voucherFundEnrollment, this.voucher).subscribe((res: any) => {
      // console.log('SAVED ::::: > ', res);
      this.sweetAlertService.success(res);
      this.setFormValidator();
      this.getVoucherList();
      this.router.navigateByUrl('asc/page/accounting/voucher-enrollment');
      this.reset();

    }, (err) => {
      // console.log(err);
      this.sweetAlertService.error(err);
    });
  }


  getCompanyList() {
    this.apiService.findAll(ENDPOINTS.company).subscribe((res: any) => {
      this.companyList = res.responseData.data;
    });
  }

  getCompanyDetails(res) {
    this.company = res;
  }

  getVoucherDetails() {
    this.process = 'update';
    // console.log('+++', this.voucher);
    this.apiService.findById(ENDPOINTS.voucher, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        // console.log(res.responseData.data);
        this.voucher = res.responseData.data
        this.signatoriesList = this.voucher.voucherSignatories;
        this.dataSourceSignatories = new MatTableDataSource<VoucherSignatories>(this.signatoriesList);
        this.dataSourceSignatories.paginator = this.formListComponent.paginator;
        this.dataSourceSignatories.sort = this.formListComponent.sort;
        this.getCompanyDetails(this.voucher.company);
        this.viewAttachmentFile();
      }
    });

  }

  onUpdateSignatory(signatory: any) {
    if (signatory.id && this.process === 'update' && this.voucher.id) {
      let data = {
        id: signatory.id,
        name: signatory.name,
        position: signatory.position,
        voucherId: this.voucher.id
      }
      this.apiService.update(ENDPOINTS.voucherSignatories, data).subscribe(result => {
        this.signatoriesList.splice(this.signatoriesList.findIndex(r => r.id === signatory.id), 1, result.responseData.data);
        this.dataSourceSignatories = new MatTableDataSource(this.signatoriesList);
        this.dataSourceSignatories.paginator = this.formListComponent.paginator;
      });
    } else {
      this.signatoriesList = signatory;
      this.dataSourceSignatories = new MatTableDataSource(this.signatoriesList);
      this.dataSourceSignatories.paginator = this.formListComponent.paginator;
    }
  }

  onDeleteSignatory(signatory: VoucherSignatories) {
    Swal.fire({
      text: 'Are you sure you want to delete this SIGNATORY?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.signatoriesList = this.signatoriesList.filter(s => s !== signatory);
        this.dataSourceSignatories = new MatTableDataSource(this.signatoriesList);
        if (this.process === 'update' && signatory.id && this.voucher.id) {
          this.apiService.deleteById(ENDPOINTS.voucherSignatories, signatory.id).subscribe((res: any) => {
            this.signatoriesList.splice(this.signatoriesList.findIndex(r => r.id === signatory.id), 1)
            this.dataSourceSignatories.paginator = this.formListComponent.paginator;
          });
        }
      }
    })
  }

  setSignatoriesList(res) {
    this.signatoriesList.push(res);
    this.dataSourceSignatories = new MatTableDataSource<VoucherSignatories>(this.signatoriesList);
    this.dataSourceSignatories.paginator = this.formListComponent.paginator;
    this.dataSourceSignatories.sort = this.formListComponent.sort;
    if (this.process === 'update' && this.voucher.id) {
      let data = {
        name: res.name,
        position: res.position,
        voucherId: this.voucher.id
      }
      this.apiService.save(ENDPOINTS.voucherSignatories, data).subscribe(res => {
        this.dataSourceSignatories.paginator = this.formListComponent.paginator;
      });
    }
    // this.resetForm === 'voucher_signatories';
    this.resetsetSignatoriesForm();
  }

  getVoucherList() {
    this.apiService.findAll(`${ENDPOINTS.voucherMonitoring}?page=1&size=30`).subscribe((res: any) => {
      // console.log(res.data);
      this.voucherList = res.data;
      this.dataSource = new MatTableDataSource<Voucher>(res.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
  }

  disabledWeekEnds = (d: Date | null): boolean => {
    const day = (d || new Date()).getDay();
    return day !== 0 && day !== 6;
  }

}
