import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CompanyVoucherEnrollmentComponent } from './company-voucher-enrollment.component';

const routes: Routes = [
  {
    path: '',
    component: CompanyVoucherEnrollmentComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CompanyVoucherEnrollmentRoutingModule { }
